<template>
    <div class="account-list">
        <div class="list-box" v-for="(item,index) in tableData.list" :key="index">
            <div class="line">
                <p style="float: left">{{item.describe}} ({{item.describe_number}})</p>
                <p style="float: right">{{item.amount}}</p>
            </div>
            <div class="line">
                <p style="float: left">{{item.createTime}}</p>
                <p style="float: right" :class="statusForm(item.status).color">{{ item.status }}</p>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "accountList",
        data () {
            return {

            }
        },
        props : {
            tableData : {
                type : Object,
                default : ()=>{
                    return {};
                }
            }
        },
        methods : {
            statusForm(value) {
                let x;
                switch (value) {
                    case "已结算":
                        x = { color: 'font-green'};
                        break;
                    default:
                        x = { color: 'font-red'};
                        break;
                }
                return x;
            },
        }
    }
</script>

<style lang="scss" scoped>
    .account-list{
        background: #fff;
        .list-box{
            padding: 15px 0;
            border-bottom: 1px solid #ccc;
            .line{
                height: 25px;
                line-height: 25px;
                padding: 0 10px;
            }
            .font-red {
                color: #f00;
            }
            .font-grey {
                color: #999;
            }
            .font-green {
                color: #00bf00;
            }
        }
        .empty-tips{
            height: 200px;
            line-height: 200px;
            color: #999;
            background: #fff;
            text-align: center;
        }
        .list-pages{
            text-align: center;
            padding: 40px 0 20px 20px;
            background: #fff;
        }
    }
</style>