<template>
    <div class="withdrawal-list">
        <div class="list-box" v-for="(item,index) in tableData.list" :key="index">
            <div class="line">
                <p style="float: left" class="p">
                    <span v-if="item.toAccount">{{item.toAccount}}</span><span v-else>{{$L('无')}}</span>
                </p>
                <p style="float: left">
                    <span v-if="item.price">{{$L('提现金额')}} {{item.price}}</span>
                </p>
                <p style="float: right">{{item.realPrice}}</p>
            </div>
            <div class="line">
                <p style="float: left" class="p">{{item.dateTime}}</p>
                <p style="float: left"  class="p"><span>{{ item.deposType == "-"?"":item.deposType }}</span>{{$L('手续费')}} {{item.fee}}</p>
                <p style="float: right"  class="p" :class="statusForm(item.status).color">{{ item.status }}</p>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "withdrawalList",
        props : {
            tableData : {
                type : Object,
                default : ()=>{
                    return {};
                }
            }
        },
        methods : {
            statusForm(value) {
                let x;
                switch (value) {
                    case "等待处理":
                        x = { color: 'font-green'};
                        break;
                    case "已结算":
                        x = { color: 'font-red'};
                        break;
                    default:
                        x = { color: 'font-grey'};
                        break;
                }
                return x;
            },
        }
    }
</script>

<style lang="scss" scoped>
    .withdrawal-list{
        background: #fff;
        .list-box{
            padding: 15px 0;
            border-bottom: 1px solid #ccc;
            .line{
                height: 25px;
                line-height: 25px;
                padding: 0 10px;
                .p{
                    width: calc((100% - 36%)/2);
                    padding-left: 10px;
                    text-overflow: ellipsis;
                    overflow: hidden;
                }
                .p:first-child{
                    width: 36%;
                    padding-left: 0px;
                }
                .p:last-child{
                    text-align: right;
                    padding-left: 10px;
                }
            }
            .font-red {
                color: #f00;
            }
            .font-grey {
                color: #999;
            }
            .font-green {
                color: #00bf00;
            }
        }
        .empty-tips{
            height: 200px;
            line-height: 200px;
            color: #999;
            background: #fff;
            text-align: center;
        }
        .list-pages{
            text-align: center;
            padding: 40px 0 20px 20px;
            background: #fff;
        }
    }
</style>