<template>
    <div class="withdrawal" >
        <vue-scroll>
            <div class="main-table">
                <div class="state">
					<el-button size="small" @click="showTimeFunc">{{ $L('时间筛选') }}</el-button>
                    <el-select v-model="messageType" size="small" v-if="selectType == 'withdraw'"
                               @change="changeTypeFunc">
                        <el-option
                                v-for="item in messageTypeList"
                                :key="item.type"
                                :label="item.name"
                                :value="item.type">
                        </el-option>
                    </el-select>
                    <router-link class="mainBgColor commission" :to="{'name':'applyWithdrawalMobile'}">{{ $L(' 申请提现') }}</router-link>
                </div>
                <div class="withdrawal-head" v-if="tableData && tableData.hasOwnProperty('statistics')">
                    <div class="top">
                        <p class="p" style="font-weight: 700;">{{ $L('总佣金') }}：{{ statistics.total }}</p>
                        <p class="p">{{ $L('分销佣金') }}：{{ statistics.no_share_out_bonus }}</p>
                        <p class="p">{{ $L('分红金额') }}：{{ statistics.share_out_bonus }}</p>
                    </div>
                    <div class="main-box">
                        <div class="main-item">
                            <i class="bbx-font mainFontColor bbx-icon-weibiaoti--11"></i>
                            <div class="info">
                                <p class="p">{{ $L('待结算') }}</p>
                                <p class="font-large">{{ statistics.no_settlement }}</p>
                            </div>
                        </div>
                        <div class="main-item">
                            <i class="bbx-font mainFontColor bbx-icon-weibiaoti--12"></i>
                            <div class="info">
                                <p class="p">{{ $L('可提现') }}</p>
                                <p class="font-large">{{ statistics.withdrawal_allow }}</p>
                            </div>
                        </div>
                        <div class="main-item">
                            <i class="bbx-font mainFontColor bbx-icon-weibiaoti--13"></i>
                            <div class="info">
                                <p class="p">{{ $L('提现中') }}</p>
                                <p class="font-large">{{ statistics.withdrawal_conduct }}</p>
                            </div>
                        </div>
                        <div class="main-item">
                            <i class="bbx-font mainFontColor bbx-icon-weibiaoti--14"></i>
                            <div class="info">
                                <p class="p">{{ $L(' 已提现') }}</p>
                                <p class="font-large">{{ statistics.withdrawal_end }}</p>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="list" v-if="tableData && tableData.hasOwnProperty('list')">
                    <div class="list-top">
                        <div class="system-header">
                            <ul class="system-header-list">
                                <li class="li mainBorderColor" @click="accountListFunc" :class="{currentClick:accountList}">{{$L(' 结算账户明细')}}</li>
                                <li class="li mainBorderColor" @click="withdrawalListFunc" :class="{currentClick:withdrawalList}">{{$L('提现记录')}}</li>
                            </ul>
                        </div>
                    </div>
                    <account-list v-loading="loading" v-if="accountList" v-bind:tableData="tableData"></account-list>
                    <withdrawal-list v-loading="loading" v-if="withdrawalList" v-bind:tableData="tableData"></withdrawal-list>
                    <div class="list-pages" v-if="tableData.list !== null && tableData.allNum > 0">
                        <el-pagination
                                small
                                :background="true"
                                @size-change="handdleSizeChange"
                                @current-change="handdleCurrentChange"
                                :current-page="currentPage"
                                :page-size="pageSize"
                                layout="prev, pager, next"
                                :total="tableData.allNum">
                        </el-pagination>
                    </div>
                    <div class="no-data" v-else>
                        {{ $L('暂无数据') }}
                    </div>
                </div>
            </div>
        </vue-scroll>
		<transition name="fade">
			<div class="time" v-if="showTime">
				<div class="close" @click="closeTimeFunc"></div>
				<div class="date">
					<calendar></calendar>
                    <inlineCalendar mode="during" :yearName="$L('年')" :monthNames="[$L('一月'), $L('二月'), $L('三月'), $L('四月'),$L( '五月'), $L('六月'),$L( '七月'), $L('八月'), $L('九月'), $L('十月'),$L( '十一月'), $L('十二月')]"
                                    :weekNames="[$L(' 周一'), $L(' 周二'), $L(' 周三'), $L(' 周四'), $L(' 周五'), $L(' 周六'), $L(' 周日')]"
                                    @change="onChange" :defaultDate="defaultDate"></inlineCalendar>
					<div class="btns">
						<el-button @click="closeTimeFunc" size="small">{{ $L('取消') }}</el-button>
						<el-button size="small" type="primary" class="mainBgColor" @click="gotoTime">{{ $L('确认') }}</el-button>
					</div>
				</div>
			</div>
		</transition>
    </div>
</template>

<script>
    import AccountList from "./withdrawalChildren/accountList";
    import WithdrawalList from "./withdrawalChildren/withdrawalList";

    export default {
        name: "withdrawal",
        data (){
            return {
                loading : false,
                errorType: 'error',
                errorTitle: this.$L('出错了'),
                errorTips: null,
                errorShow: false,
                tableData: {},
                statistics:{},
                showSearch:false,
                messageType:-999,
                messageTypeList:[
                    {
                        type:-999,
                        name:this.$L('全部状态')
                    },
                    {
                        type:0,
                        name:this.$L('待结算')
                    },
                    {
                        type:1,
                        name:this.$L('已结算')
                    },
                    {
                        type:-1,
                        name:this.$L('已拒绝')
                    }
                ],
                selectType:'account',
                currentPage:1,
                pageSize:10,
                accountList: true,
                withdrawalList: false,
                dtCreateStart: '',
                dtCreateEnd: '',
				showTime:false,
				timeArr:[],
				defaultDate:[]
            }
        },
        components : {
            WithdrawalList,
            AccountList,
        },
        created () {
            this.getData();
        },
        methods : {
            showError(title, tips, type) {
                if (type === 'warning') {
                    this.errorType = 'warning';
                } else {
                    this.errorType = 'error';
                }
                if (title && title !== '') {
                    this.errorTitle = title;
                } else {
                    this.errorTitle = this.$L('出错了');
                }
                if (tips && tips !== '') {
                    this.errorTips = tips;
                } else {
                    this.errorTips = null;
                }
                this.loading = false;
                this.errorShow = true;
            },
            getData() {
                const _self = this;
                _self.loading = true;
                let arr = {
                    p: _self.currentPage,
                    num: _self.pageSize,
                    isMobile:1,
                    selectType:_self.selectType,
                    status: _self.messageType,
                    dtCreateStart: _self.dtCreateStart,
                    dtCreateEnd: _self.dtCreateEnd
                };
                _self.$api('distributionMyCommission', arr, function (reData) {
                    if (reData.flag === 1) {
                        _self.tableData = reData.data;
                        _self.statistics =  reData.data.statistics;
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name: 'distributorMain'});
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        _self.$router.push({name: 'distributorMain'});
                    });
                });
            },
            handdleSizeChange(val){
                this.pageSize = val;
                this.getData();
            },
            handdleCurrentChange(val){
                this.currentPage = val;
                this.getData();
            },
            accountListFunc(){
                this.accountList = true;
                this.withdrawalList = false;
                this.selectType = "account";
                this.currentPage = 1;
                this.getData();
            },
            withdrawalListFunc(){
                this.withdrawalList = true;
                this.accountList = false;
                this.selectType = "withdraw";
                this.currentPage = 1;
                this.getData();
            },
            changeTypeFunc(val){
                this.type = val;
                this.getData();
            },
			showTimeFunc(){
				this.showTime = !this.showTime;
			},
			onChange(date) {
				let arr = JSON.stringify(date.map((item) => item.format('YYYY-MM-DD')));
				arr = arr.replace(/"|\[|\]/g,'');
				arr = arr.split(',');
				this.defaultDate = arr;
				this.dtCreateStart = arr[0];
				this.dtCreateEnd = arr[1] ? arr[1]+' 23:59:59' : '';
			},
			closeTimeFunc(){
				this.showTime = false;
				this.defaultDate = [];
				this.dtCreateStart = '';
				this.dtCreateEnd = '';
				this.getData();
			},
			gotoTime(){
				this.showTime = false;
				this.getData();
			},
        },
    }
</script>

<style lang="scss" scoped>
    @import "../../../assets/css/common/lib.scss";
    .withdrawal{

        .search-box{
            position: absolute;
            width: 100%;
            height: 45px;
            line-height: 45px;
            background-color: #fff;
            top: -45px;
            left: 0;
            z-index: 100;
            border-bottom: 1px solid #E4E7ED;
            @include diy-animation-show(top);
            .el-select{
                margin-left: 10px;
                width: 100px;
                vertical-align: bottom;
            }
            .time-search{
                width: 230px;
            }
            .search-close{
                width: 45px;
                height: 45px;
                text-align: center;
                line-height: 45px;
                position: absolute;
                top: 0;
                right: 0;
                z-index: 100;
                font-size: 18px;
                color: #909399;
                &:active{
                    color: #606266;
                }
            }

        }
        .main-table{
            position: relative;
            z-index: 0;
            @include diy-animation-show(margin);
            .state{
				padding: 0 10px;
                height: 45px;
                line-height: 45px;
                margin-bottom: 10px;
                background-color: #fff;
                .el-select{
                    margin-left: 10px;
                    width: 100px;
                    vertical-align: bottom;
					.el-input__inner{
						line-height: 31px;
					}
                }
                .time-search{
                    width: 230px;
                }
                .commission{
                    display: inline-block;
                    width: 75px;
                    height: 32px;
                    color: #fff;
                    line-height: 32px;
                    text-align: center;
                    float: right;
                    margin-top: 7px;
                }
            }
            .withdrawal-head{
                position: relative;
                background: #fff;
                .top{
                    padding: 10px;
                    display: flex;
                    justify-content: flex-start;
                    flex-wrap: wrap;
                    border-bottom: 1px solid #ccc;
                    .p{
                        width: 50%;
                        margin-bottom: 10px;
                        &:last-child{
                            margin-bottom:0;
                        }
                    }
                }
                .main-box{
                    padding: 20px 10px 10px;
                    display: flex;
                    justify-content: space-between;
                    flex-wrap: wrap;
                    .main-item{
                        width: 50%;
                        margin-bottom: 10px;
                        .bbx-font{
                            font-size: 40px;
                            vertical-align: text-bottom;
                            margin-right: 10px;
                        }
                        .info{
                            display: inline-block;
                            width: calc(100% - 50px);
                            overflow: hidden;
                            text-overflow: ellipsis;
                            white-space: nowrap;
                            .p {
                                font-size: 14px;overflow: hidden;
                                text-overflow: ellipsis;
                                white-space: nowrap;

                            }
                            .font-large{
                                font-size: 16px;
                                font-weight: 700;
                                overflow: hidden;
                                text-overflow: ellipsis;
                                white-space: nowrap;
                            }
                        }
                    }
                }
            }
            .list{
                margin-top: 10px;
                .list-top{
                    height: 40px;
                    .system-header{
                        height: 40px;
                        float: left;
                        .system-header-list{
                            @extend .no-select;
                            .li{
                                width: 110px;
                                height: inherit;
                                line-height: 40px;
                                display: inline-block;
                                text-align: center;
                                font-size: 12px;
                                background: #CCCCCC;
                                border-color: #f8f8f9;
                                cursor: pointer;
                            }
                            .currentClick {
                                background-color: #fff;
                            }
                        }

                    }
                    .right{
                        float: right;
                        .time-search{
                            vertical-align: bottom;
                        }
                        .font{
                            padding-left: 20px;
                            font-size: 16px;
                        }

                        .input-with-select{
                            width: calc(100% - 480px);
                            padding-left: 10px;
                            /deep/ .el-input-group__append{
                                font-size: 14px;
                                color: #333;
                                padding: 0 10px;
                                background: #fff;
                                border-left-color: #fff;
                            }
                        }
                    }
                }
                .list-pages{
                    text-align: center;
                    padding: 20px 0;
                    background-color: #fff;
                }
                .no-data{
                    position: relative;
                    width: 100%;
                    height: 170px;
                    line-height: 170px;
                    text-align: center;
                    background-color: #ffffff;
                }
                .currentClick {
                    background-color: #fff;
                }
            }
        }

        .error {
            position: absolute;
            z-index: 200;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: #FFFFFF;
            overflow: hidden;

            .error-tips {
                position: absolute;
                top: 50%;
                left: 50%;
                width: 320px;
                transform: translate(-50%, -50%);
            }
        }
        &.showSearch{
            .search-box{
                top: 0;
            }
            .main-table{
                margin-top: 55px;
            }
        }
		
		.time{
			height: 100%;
			position: fixed;
			z-index: 1000;
			top: 0;
			right: 0;
			left: 0;
			bottom: 0;
			background: rgba(0, 0, 0, 0.6);
			.date{
				width: 100%;
				position: absolute;
				top: 50%;
				transform: translateY(-50%);
				.btns{
					background-color: #fff;
					text-align: right;
					padding: 5px 10px;
				}
			}
			.close{
				width: 100%;
				height: 100%;
				top: 0;
				right: 0;
				left: 0;
				bottom: 0;
			}
		}
		.fade-enter,
		.fade-leave-to{
		   opacity: 0;
		}
		.fade-enter-active,
		.fade-leave-active{
		   transition: opacity 0.5s
		}
    }

    @media screen and (max-width: 320px) {
        .main-box{
            .main-item{
                padding-left: 5px!important;
            }
        }
    }
</style>